body {
    background-image: url(../img/background.png);
    background-attachment: fixed;
}

.aktina_gallery {
    position: absolute;
    top: 10vh;
    left: 3vw;
    width: 89vw;
    height: auto;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(6, 248px);
    column-gap: 10px;
    row-gap: 10px;
}

.aktina_gallery figure {
    overflow: hidden;
}

.aktina_gallery figure > a{
    width: 100%;
    height: 100%;
}

.aktina_gallery figure > a > div {
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center center;
}

.aktina_gallery figure:nth-child(1) > a > div {
    background-image: url("../img/gallery/1-1.jpeg");
}

